import React, {useState} from 'react';
import {Table} from 'react-bootstrap';
import { connect } from 'react-redux';

function Cart(props){

    let [카트, 카트변경] = useState(props.state);
    
    return (
        <div>
            <Table striped bordered hover>
            <tr>
                <th>#</th>
                <th>상품명 Name</th>
                <th>수량</th>
                <th>버튼</th>
            </tr>
            {
                카트.map((i,j)=>{
                    return (
                    <List 카트={카트[j]}></List>  
                    );
                }
                )
            }
            </Table>

        </div>
    )
}

// store 안의 state를 props화 시켜주는 함수
function 함수명(state){
 return {
    state : state
 }
}

function List(props){
    return (
        <tr>
            <td>{props.카트.id}</td>
            <td>{props.카트.name}</td>
            <td>{props.카트.quantity}</td>
            <td><button className="btn btn-primary">구매꼬</button></td>
        </tr>

    )
}

export default connect(함수명)(Cart);
//export default Cart;